ul{
    list-style-type: none;
    color: aliceblue;
    margin: 0;
    padding: 0;
}



nav {
    position: relative;
    z-index: 2; /* Set a higher z-index for the nav container */
}

nav a {
    position: relative; /* Ensure z-index works properly */
    z-index: 2; /* Set a higher z-index for the navigation links */
}

.dropdown1:hover{
    opacity: 1;
    visibility: visible;
    max-width: 150px;
    width: 200px;
    max-height: 80px;
    transition: max-height 1s;
    transition: all 1s;
}


.dropdown1 {
    display: grid;
    opacity: 1;
    grid-column-start: -8;
    grid-row-start: 2;
    justify-content: start;
    align-self: start;
    overflow: hidden;
    transform-origin: top;
    border-radius: 8px;
    max-height: 0;
    max-width: 0;
    width: 200px;
    white-space: nowrap;
    height: 200px;
    transition: max-width 1s, max-height 1s, opacity 8s, visibility 0s 1s;
    background-color: blue;
}

nav a:nth-child(2):hover + .dropdown1 {
    opacity: 1;
    visibility: visible;
    max-width: 150px;
    width: 200px;
    max-height: 80px;
    transition: max-height 1s;
    transition: all 1s; /* Adjust transition timing function, duration, and delay */
}

nav a:focus {
    color: yellow;
  }

nav a{
    text-decoration: none;
    color: white;
    transition: all 0.5s ease;
}

nav a:hover{
    color: yellow;
    transition: all 1s;
}

nav{
    display: grid;
    grid-template-columns: 40px 40px 40px 20px 70px 40px 30px 40px;
    grid-template-rows: 40px 100px;
    grid-column-start: 2;
    justify-content: center;
    align-items: center;
    column-gap: 30px;
    row-gap: 0px;
}

.container1{
    display: flex;
    justify-content: end;
    align-items: center;
    margin-bottom: 20px;
    margin-top: 20px;
    margin-right: 10vw;
    gap: 20px;
}
.container1 i {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    height: auto;
    border-radius: 50%;
    color: white;
    transition: all 0.5s ease;
}

.container1 i:nth-child(1):hover {
    color: hsl(240, 100%, 50%);
    transition: all 1s;
}

.container1 i:nth-child(2):hover {
    color: hsl(193, 100%, 50%);
    transition: all 1s;
}

.container1 i:nth-child(3):hover {
    color: hsl(343, 77%, 72%);
    transition: all 1s;
}

.container1 i:nth-child(4):hover {
    color: hsl(0, 94%, 31%);
    transition: all 1s;
}

.container2{
    display: grid;
    grid-template-columns: 1fr 1fr;
}